.wp-block-hp-framer-svg-hp-framer-svg {
	font-size: 1em;
	background: #ffff001a;
	padding: 1em;

	&.dark-theme {
		background: #333;
		color: #fff;

		button {
			background: #555;
			color: #fff;
			border: 1px solid #777;
		}

		p {
			color: #ddd;
		}
	}
}

#hp-framer-svg{
	background-color: transparent;
	margin: 0;
	padding: 0;
}

.hp--framer--svg{
	height: 50vh;
	min-height: 550px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border: 2px solid blue;
}
.hp--framer--svg__svg{
	width: 75px;
}
